import Table from '../../mo/models';

/**
 * 是否属于表格元素（可配置是否包括分栏）
 */
export function isTableNode(maybeTable: any, excludeColumns = false) {
  if (Table.isTable(maybeTable)) {
    if (excludeColumns) {
      return !maybeTable.data.sr;
    }
    return true;
  }
  return false;
}

/**
 * 是否属于表格元素 (不包括分栏)
 */
export function isPureTableNode(maybeTable: any) {
  return isTableNode(maybeTable) && !maybeTable?.data?.sr;
}
